import React, { Component } from 'react';
import { Row, Col, Form, Select, Checkbox } from 'antd';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { YearSemesterInput } from 'components/common';
import { BtnSearch, BtnClear } from 'components/common/button';
import { timesRegister } from 'dump/data';
import './style.scss';

@Form.create({ name: 'formClassList' })
class FormInfoCommon extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };
  
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
      updateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate
            form={this.props.form}
            studyField={{ isShow: false }}
            classes={{ isShow: false }}
            optionFieldDecorator={optionFieldDecorator}
          />
          <YearSemesterInput
            form={this.props.form}
            span={5}
            semesterSpan={4}
            optionFieldDecorator={optionFieldDecorator}
          />
          {/* <Col span={6}>
            <Form.Item label="Học kỳ:">
              <InputNumber min={1} max={3} defaultValue={1} style={{ width: '100%' }} />
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Năm học:">
              {getFieldDecorator('schoolYearsTime', {})(
                <Select>
                  {schoolYearsTime.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col> */}
          <Col span={5}>
            <Form.Item label="Đợt:">
              {getFieldDecorator('registerSessionId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn đợt đăng ký',
                  },
                ],
              })(
                <Select>
                  {timesRegister.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item>
              {getFieldDecorator('isCancelled', {
                valuePropName: 'checked',
                initialValue: false
              })(
                <Checkbox>Lớp hủy</Checkbox>
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



